"use client"

import Link from "next/link"
import { usePathname } from "next/navigation"
import { useClerk } from "@clerk/nextjs"
import { cn } from "@/lib/utils"
import { BarChart3, Box, Home, LayoutDashboard, LogOut, Settings, ShoppingCart, Users } from "lucide-react"

const sidebarLinks = [
  {
    title: "Dashboard",
    href: "/admin/dashboard",
    icon: LayoutDashboard,
  },
  {
    title: "Products",
    href: "/admin/dashboard/products",
    icon: Box,
  },
  {
    title: "Orders",
    href: "/admin/dashboard/orders",
    icon: ShoppingCart,
  },
  {
    title: "Customers",
    href: "/admin/dashboard/customers",
    icon: Users,
  },
  {
    title: "Analytics",
    href: "/admin/dashboard/analytics",
    icon: BarChart3,
  },
  {
    title: "Settings",
    href: "/admin/dashboard/settings",
    icon: Settings,
  },
]

export function DashboardSidebar() {
  const pathname = usePathname()
  const { signOut } = useClerk()

  const handleSignOut = () => {
    signOut(() => {
      window.location.href = "/admin/login"
    })
  }

  return (
    <aside className="hidden w-64 flex-shrink-0 border-r border-gray-200 bg-white md:flex md:flex-col">
      <div className="flex h-14 items-center border-b border-gray-200 px-4">
        <Link href="/admin/dashboard" className="flex items-center space-x-2">
          <Home className="h-6 w-6 text-emerald-600" />
          <span className="text-lg font-bold">Standing Desk</span>
        </Link>
      </div>
      <nav className="flex-1 space-y-1 px-2 py-4">
        {sidebarLinks.map((link) => (
          <Link
            key={link.href}
            href={link.href}
            className={cn(
              "group flex items-center rounded-md px-2 py-2 text-sm font-medium",
              pathname === link.href
                ? "bg-emerald-100 text-emerald-700"
                : "text-gray-600 hover:bg-gray-50 hover:text-gray-900",
            )}
          >
            <link.icon
              className={cn(
                "mr-3 h-5 w-5 flex-shrink-0",
                pathname === link.href ? "text-emerald-600" : "text-gray-400 group-hover:text-gray-500",
              )}
            />
            {link.title}
          </Link>
        ))}
      </nav>
      <div className="border-t border-gray-200 p-4">
        <button
          onClick={handleSignOut}
          className="flex w-full items-center rounded-md px-2 py-2 text-sm font-medium text-gray-600 hover:bg-gray-50 hover:text-gray-900"
        >
          <LogOut className="mr-3 h-5 w-5 text-gray-400" />
          Sign out
        </button>
      </div>
    </aside>
  )
}
